import { HubDate, HubDateProps } from '@hubert/molecules';
import {
  FUNCTION,
  HEIGHT,
  ID,
  ON_CHANGE,
  TYPO,
  VALUE,
  WIDTH,
  factoryMeta
} from '~/utils';

export const HubDateConfig = factoryMeta<HubDateProps>({
  title: 'Design System/Molecules/HubDate',
  component: HubDate,
  description: 'import { HubDate } from "@hubert/molecules";',
  args: {},
  props: {
    id: ID,
    editable: {
      description:
        'Se false não permite digitar no input, somente selecionar no HubCalendar',
      defaultValue: true,
      type: 'boolean'
    },
    width: WIDTH,
    height: HEIGHT,
    inputTypo: TYPO,
    headless: {
      description: 'Deixa o calendario sem o header (btn avançar e volta)',
      type: 'boolean'
    },
    initialMode: {
      description: 'Modo de apresentação inicial do calendario',
      type: 'inline-radio',
      options: ['days', 'months', 'years']
    },
    mandatoryMode: {
      description:
        'Bloqueia outros modos, deixam possivel somente visualizar o calendario utilizando o "initialMode"',
      type: 'boolean'
    },
    range: {
      description: 'Selecione sempre duas datas, inicio e fim',
      type: 'boolean'
    },
    withActions: {
      description: 'Mostra o rodapé do calendario com as ações',
      type: 'boolean'
    },
    value: VALUE,
    onChange: ON_CHANGE,
    onCalendarChange: FUNCTION,
    onClose: FUNCTION
  }
});
